import Citys from '../city.js';
import amapFile from '../amap/amap-wx.js'
export default {
	components: {},
	props: {},

	computed: {
		hotCity() {
			return Citys.hotCity;
		},

		citys() {
			return Citys.cities;
		}
	},

	data() {
		return {
			statusBarHeight: this.statusBarHeight,
			ImgUrl: this.ImgUrl,
			letter: [],
			selectLetter: '',
			searchValue: '',
			scrollIntoId: '',
			list: [],
			tId: null,
			searchList: [],
			showMask: false,
			disdingwei: true,
			Visit: [], //最近访问
			position: '',
			longitude: '', //经度
			latitude: '', //纬度
			seconds: 3,
			po_tips: '重新定位',
			markersData:{
			        latitude: '',//纬度
			        longitude: '',//经度
			        key: "134acca8871d797b16a9a6a0a873bab5"//申请的高德地图key（申请的web key）
			      },
			      gpsCode:'',
		}
	},

	created() {
		//获取存储的最近访问
		var that = this
		uni.getStorage({
			key: 'Visit_key',
			success: function(res) {
				that.Visit = res.data
			}
		});
		//获取定位 经度纬度
		that.getWarpweft()
		//获取city.js 的程序字母
		var mu = ['a', 'b', 'c', 'd', 'e', 'f', 'g', 'h', 'j', 'k', 'l', 'm', 'n', 'p', 'q', 'r', 's', 't', 'w', 'x', 'y',
			'z'
		];
		var tmp = [];
		for (var i = 0; i < mu.length; i++) {
			var item = mu[i];
			for (var j = 0; j < this.citys.length; j++) {
				var py = this.citys[j].py;
				if (py.substring(0, 1) == item) {
					if (tmp.indexOf(item) == -1) {
						this.list[i] = [this.citys[j]];
						tmp.push(item);
						this.letter.push(item.toUpperCase());
					} else {
						this.list[i].push(this.citys[j]);
					}
				}
			}
		}
	},
	methods: {
		//定位授权
		getId(index) {
			return this.letter[index];
		},
		getAuthorizeInfo(a="scope.userLocation"){  //1. uniapp弹窗弹出获取授权（地理，个人微信信息等授权信息）弹窗
				var _this=this;
				uni.authorize({
					scope: a,
					success() { //1.1 允许授权
						_this.getLocationInfo();
					},
					fail(){    //1.2 拒绝授权
						// console.log("你拒绝了授权，无法获得周边信息")
					}
				})
			},

		scrollTo(letter) {
			this.showMask = true
			this.selectLetter = letter == 'hot' ? '最' : letter
			setTimeout(() => {
				this.showMask = false
			}, 300);
			this.scrollIntoId = letter;
		},


		query(source, text) {
			let res = [];
			var self = this;
			res = source.filter(item => {
				const arr = [];
				let isHave = false;
				Object.keys(item).forEach(prop => {
					const itemStr = item[prop];
					self.isString(itemStr) &&
						itemStr.split(',').forEach(val => {
							arr.push(val);
						});
				});
				arr.some(val => {
					isHave = new RegExp('^' + text).test(val);
					return isHave;
				});
				return isHave;
			});
			
			return res;
		},

		isString(obj) {
			return typeof obj === 'string';
		},

		onInput(e) {
			const value = e.target.value;
			
			if (value !== '' && this.citys && this.citys.length > 0) {
				const queryData = this.query(this.citys, String(value).trim());
				this.searchList = queryData;
				this.disdingwei = false
			} else {
				this.searchList = [];
				this.disdingwei = true
			}
		},
           
		back_city(item) {
			if (item) {
				this.$emit('back_city', item);
				//unshift 把数据插入到首位，与push相反
				this.Visit.unshift(item)
				this.searchValue = "";
				this.disdingwei = true
				var arr = this.Visit
				//数组去重
				function distinct(arr) {
					let newArr = []
					for (let i = 0; i < arr.length; i++) {
						if (newArr.indexOf(arr[i]) < 0) {
							newArr.push(arr[i])
						}
					}
					return newArr
				}
				this.Visit = distinct(arr)
				// console.log(this.Visit, "---最近访问")
				uni.setStorage({
					key: 'Visit_key',
					data: this.Visit
				});
			} else {
				this.$emit('back_city', 'no');
			}
		},
		back_city2(){
			this.$emit('back_city', 'yes');
		},
		
		getWarpweft() {
			var that = this
			that.po_tips = '定位中...'
			uni.getLocation({
				type:'wgs84',
				geocode:true,
				success: function(res) {
					// console.log(res)
						uni.setStorage({
							key: 'app_position',
							data:res.address,
						})
						that.loadCity();//小程序-调用高德
					},
			})
		},
		//把当前位置的经纬度传给高德地图，调用高德API获取当前地理位置
		    loadCity:function(){
		      var myAmapFun = new amapFile.AMapWX({ 
				  key: this.markersData.key,
				  });
		      // console.log(myAmapFun);
		      myAmapFun.getRegeo({
		        success:(data) =>{
					// console.log(data)
		          this.position =data[0].regeocodeData.addressComponent.city
		          uni.setStorage({
		          	key: 'wx_position',
		          	data:this.position
		          })
				  this.po_tips = '重新定位'
		        },
		        fail:()=>{
					this.po_tips = '重新定位'
				}
		      });
		 
		    },
	}
};
